/*! FSA Style v0.2.7 | http://usda-fsa.github.io/fsa-design-system/ */


html {
  box-sizing: border-box;
}

*, *::after, *::before {
  box-sizing: inherit;
}

.usa-sr-only {
  position: absolute;
  left: -999em;
}

/*////////////////////////////////////////////////////////*/
/*//                                                    //*/
/*//    BREAKPOINT BAR                                  //*/
/*//                                                    //*/
/*////////////////////////////////////////////////////////*/
body::before {
  display: none;
  text-align: center;
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: 99;
  content: '0px - 239px';
  color: white;
  text-shadow: 0 -1px 0 rgba(0, 0, 0, 0.6);
  background: rgba(255, 0, 0, 0.8);
  padding: 2px 2px 3px;
  font-family: Consolas, Menlo, Monaco, "Lucida Console", "Courier New", monospace;
  font-size: 14px;
  line-height: 1;
}

@media (min-width: 20em) {
  body::before {
    background: black;
    content: 'S: 320px - 559px';
  }
}

@media (min-width: 35em) {
  body::before {
    content: 'M: 560px - 863px';
    background: rgba(204, 102, 0, 0.8);
  }
}

@media (min-width: 54em) {
  body::before {
    content: 'L: 865px +';
    background: rgba(50, 20, 50, 0.8);
  }
}

@media (min-width: 78.75em) {
  body::before {
    content: 'XL: 1260px +';
    background: rgba(0, 70, 0, 0.8);
  }
}

html:hover body::before {
  display: block;
}

.is-showing-breakpoints body::before {
  display: block;
}

@keyframes peekInFromAbove {
  from {
    opacity: 0;
    transform: translateY(-200%);
  }
  to {
    opacity: 1;
    transform: translateY(0%);
  }
}

